@use "uswds-core" as *;


img { height: auto;}

header { min-height: 220px;
   @include at-media-max($theme-header-min-width) {
      min-height: 48px;
   }

  
}

.hero-wrapper {
   min-height: 533px;
   max-height: 534px;
   overflow: hidden;

   @media(max-width: 280px)  {
      min-height: 762px;
      max-height: 763px;
   }

   @media(max-width: 340px)  {
      min-height: 670px;
      max-height: 671px;
   }

   @media(max-width: 374px)  {
      min-height: 608px;
      max-height: 609px;
   }

   @media(max-width: 400px)  {
      min-height: 571px;
      max-height: 572px;
   }

   &.usa-header {
      max-height: none;
   }
  
}